.blocker {
	z-index: 100;

	@media (max-width: 767px) {
		padding: 0;
	}
}

.modal {
	z-index: 1001;
	padding: 76px 69px 82px;
	width: 90%;
	max-width: 970px;
	background-color: #f9f6f1;

	@media (max-width: 1199px) {
		padding-right: 35px;
		padding-left: 35px;
	}

	@media (max-width: 767px) {
		border-radius: 0;
		width: 100%;
		min-height: 100vh;
		min-height: calc(var(--vh, 1vh) * 100 + 70px);
	}

	.title {
		margin-bottom: 48px;

		span {
			width: 90px;
		}
	}

	ol {
		margin-bottom: 73px;
		margin-left: 20px;
		padding: 0;

		li {
			margin-bottom: 20px;
		}
	}

	.info-block {
		display: flex;

		&::before {
			content: "";
			display: inline-block;
			width: 56px;
			height: 34px;
			background-image: url("../images/info@2x.png");
			background-position-y: 2px;
			background-size: 32px 32px;
			background-repeat: no-repeat;
		}

		p {
			margin: 0;
			max-width: 555px;
		}
	}

	a.close-modal {
		right: 30px;
		top: 30px;
		background-image: url("data:image/png;base64,");
		background-size: 32px 32px;

		@media (max-width: 767px) {
			right: 15px;
			top: 15px;
			background-size: 16px 16px;
		}
	}
}

.modal.modal-all-address {
	max-height: 90vh;
}

.modal.all-address-mobile {
	max-height: 100vh;

	.title {
		margin-bottom: 0;
		width: 100%;
	}

	p {
		margin-bottom: 11px;

		&.address {
			position: relative;
			margin-bottom: 20px;
			font-weight: 600;

			span {
				font-weight: 600;
			}
		}

		&.open24 {
			padding-left: 45px;
		}

		&.open24::before {
			@include sprite("open24");

			content: "";
			position: absolute;
			left: 0;
			top: -4px;
			margin-right: 28px;
			margin-bottom: 4px;
		}

		span {
			font-weight: 500;
		}
	}

	.address-wrapper {
		overflow-y: scroll;
		max-height: calc(100vh - 160px);
	}

	.address-item {
		border-bottom: 1px solid #cbbabb;
		padding: 32px 0;
		width: 100%;
	}
}

.modal.modal-menu {
	padding: 160px 95px 195px;
	max-width: 1400px;

	@media (max-width: 1199px) {
		padding-right: 35px;
		padding-left: 35px;
	}

	@media (max-width: 991px) {
		padding: 80px 15px;
	}
}
